@mixin desktop {
  @media (max-width: $width-desktop-max - 1px) {
    @content;
  }
}

// ÐÐµÐ½ÑÑÐµ 1276
@mixin desktop-medium {
  @media (max-width: $width-desktop-medium - 1px) {
    @content;
  }
}

// ÐÐµÐ½ÑÑÐµ 1024
@mixin tablet {
  @media (max-width: $width-desktop - 1px) {
    @content;
  }
}

// ÐÐµÐ½ÑÑÐµ 768
@mixin mobile-l {
  @media (max-width: $width-tablet - 1px) {
    @content;
  }
}

// ÐÐµÐ½ÑÑÐµ 480
@mixin mobile {
  @media (max-width: $width-mobile-large - 1px) {
    @content;
  }
}

// Ð¡ÐÐ ÐÐ¡ Ð¡Ð¢ÐÐÐÐ

@mixin list-reset {
  margin: 0;
  padding: 0;
  list-style: none;
}

@mixin link-reset {
  text-decoration: none;
  color: inherit;
}

// Ð¨Ð ÐÐ¤Ð¢Ð«
@mixin font-face($name, $filename, $weight) {
  @font-face {
    font-family: $name;
    font-weight: $weight;
    font-style: normal;
    font-display: swap;
    src: url("../fonts/"+$filename+".woff2") format("woff2"),
      url("../fonts/"+$filename+".woff") format("woff");
  }
}

// Ð ÐÐ¢ÐÐÐ
@mixin retina {
  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    @content;
  }
}

// ÐÐÐÐ¢ÐÐÐÐÐ 
@mixin container {
  margin: 0 auto;
  max-width: 1470px;
  padding-left: 30px;
  padding-right: 30px;

  @media (max-width: $width-tablet - 1px) {
    padding: 0;
  }
}
